/* SECTION 1 */

.sectionOne .desc {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.sectionOne h2 {
    font-size: 70px;
}

.sectionOne .imgs img {
    width: 400px;
    border-top-left-radius: 200px;
    border-top-right-radius: 200px;
    border: 3px solid transparent;
    outline: 4px solid rgba(0, 0, 0, 0.608);
    height: 600px;
}

.sectionOne .details {
    margin-top: -80px;
    
}

@media only screen and (max-width: 995px) {
    .sectionOne .details {
        margin-top: 0px;
    }  
}

